import React, { useState } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';
import Sidebar from './Sidebar';

function ChangePassword({ token, role, handleLogout }) {
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (newPassword !== confirmPassword) {
      alert('Hasa nie s identyczne');
      return;
    }

    try {
      const response = await axios.post('https://nodex.goplusbet.pl/api/users/change-password', {
        newPassword
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });
      alert('Haso zostao zmienione');
    } catch (error) {
      alert('Nie udao si zmieni hasa');
      console.error(error);
    }
  };

  return (
    <>
    <Sidebar handleLogout={handleLogout} username={sessionStorage.getItem('username')} />
    <div className="container my-5">
      <div className="row justify-content-center">
        <div className="col-md-8">
          <div className="card shadow">
            <div className="card-header">
              <h5>Zmie haso</h5>
            </div>
            <div className="card-body">
              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label htmlFor="newPassword" className="form-label">Nowe haso</label>
                  <input
                    type="password"
                    className="form-control"
                    id="newPassword"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                    required
                    placeholder="Wpisz nowe haso"
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="confirmPassword" className="form-label">Potwierd nowe haso</label>
                  <input
                    type="password"
                    className="form-control"
                    id="confirmPassword"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    required
                    placeholder="Potwierd nowe haso"
                  />
                </div>
                <button type="submit" className="btn btn-success">Zmie haso</button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
  );
}

export default ChangePassword;
