// /components/SetPassword.js
import React, { useState } from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';

function SetPassword() {
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const { token } = useParams();
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (password !== confirmPassword) {
            alert('Hasa nie pasuj do siebie, sprbuj ponownie.');
            return;
        }
        try {
            const response = await axios.post('https://nodex.goplusbet.pl/api/users/set-password', { password, token });
            alert('Twoje haso zostao pomylnie ustawione!');
            navigate('/');
        } catch (error) {
            alert('Bd przy ustawianiu hasa, sprbuj ponownie.');
        }
    };

    return (
        <div className="container set-pass" style={{ maxWidth: '400px' }}>
            <h2>Ustaw haso</h2>
            <form onSubmit={handleSubmit}>
                <div className="mb-3">
                    <label htmlFor="password" className="form-label">Nowe haso</label>
                    <input
                        type="password"
                        className="form-control"
                        id="password"
                        value={password}
                        onChange={e => setPassword(e.target.value)}
                        required
                    />
                </div>
                <div className="mb-3">
                    <label htmlFor="confirmPassword" className="form-label">Potwierd nowe haso</label>
                    <input
                        type="password"
                        className="form-control"
                        id="confirmPassword"
                        value={confirmPassword}
                        onChange={e => setConfirmPassword(e.target.value)}
                        required
                    />
                </div>
                <button type="submit" className="btn btn-success">Zatwierd</button>
            </form>
        </div>
    );
}

export default SetPassword;
